@font-face {
  font-family: SansitaOne;
  src: url(../../Assest/SansitaOne.ttf);
}
@font-face {
  font-family: Roboto-Light;
  src: url(../../Assest/Roboto-Light.ttf);
}
@media only screen and (max-width: 760px) {
  .burger-menu {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 40px;
    z-index: 4;
  }
  nav {
    display: none;
  }
  .logo {
    color: green;
    text-align: center;
    position: absolute;
    top: 15px;
    left: 50%; /* Position the left edge at 50% of the viewport width */
    transform: translateX(-50%); /* Center horizontally */
    font-size: 40px;
    font-family: "SansitaOne";
    font-style: normal;
    font-variant: normal;
    font-weight: 900;
    line-height: 50px;
    z-index: 2;
  }

  nav.open {
    display: block;
    background-color: #25841c;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 5px;
    right: 5px;
    padding-left: 40px;
    padding-top: 50px;
    padding-right: 10px;
    height: 320px;
    z-index: 3;
    border-radius: 20px;

    ul {
      list-style-type: none;

      li {
        display: flex;
        justify-content: flex-end;
        margin-right: 5px;
        margin-top: 15px;
        font-size: 25px;
        a {
          text-decoration: none;
          color: white;
          font-family: "Roboto-Light";
        }
      }
    }
  }
}
.white-close svg {
  fill: white;
}
